import React, { useState, useEffect } from "react";
import { Button, Table, Modal, Form } from "react-bootstrap";
import { FaEdit, FaTrash, FaUndo, FaFileInvoice, FaTags } from "react-icons/fa";
import Layout from "@/components/Layout"; // Layout Component
import PaginationComponent from "@/components/PaginationComponent"; // Custom Pagination Component
import axios from "axios";
import styles from "../styles/shipping.module.css"; // Custom styles

const ShippingPage = () => {
  const [orders, setOrders] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [modalType, setModalType] = useState("");
  const [formData, setFormData] = useState({});
  const [processing, setProcessing] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage] = useState(5);

  const totalPages = Math.ceil(orders.length / itemsPerPage);
  const paginatedOrders = orders.slice(
    (currentPage - 1) * itemsPerPage,
    currentPage * itemsPerPage
  );

  // Fetch orders on component mount
  useEffect(() => {
    fetchOrders();
  }, []);

  const fetchOrders = async () => {
    try {
      const response = await axios.post("/api/shiprocket", {
        action: "fetchOrders",
        payload: {}, // Add any necessary payload for fetching orders
      });
      setOrders(response.data.orders);
    } catch (error) {
      console.error("Error fetching orders:", error.response?.data || error.message);
    }
  };

  const handleFormChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleAction = async (actionType, payload) => {
    setProcessing(true);
    try {
      const response = await axios.post("/api/shiprocket", {
        action: actionType,
        payload,
      });
      alert(`${actionType} completed successfully!`);
      fetchOrders(); // Refresh orders after action
    } catch (error) {
      console.error(`Error during ${actionType}:`, error.response?.data || error.message);
      alert(`Failed to ${actionType}. Check console for details.`);
    } finally {
      setProcessing(false);
      setShowModal(false);
    }
  };

  return (
    <Layout>
      <div className="container mt-5">
        <h1 className={styles.pageTitle}>Shipping Management</h1>
        <Table striped bordered hover>
          <thead>
            <tr>
              <th>Order ID</th>
              <th>Recipient Name</th>
              <th>Address</th>
              <th>Status</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {paginatedOrders.length === 0 ? (
              <tr>
                <td colSpan="5" className="text-center">
                  No data available
                </td>
              </tr>
            ) : (
              paginatedOrders.map((order) => (
                <tr key={order.id}>
                  <td>{order.id}</td>
                  <td>{order.recipientName}</td>
                  <td>{order.address}</td>
                  <td>{order.status}</td>
                  <td>
                    <Button
                      variant="secondary"
                      size="sm"
                      onClick={() => {
                        setModalType("updateOrder");
                        setFormData(order);
                        setShowModal(true);
                      }}
                    >
                      <FaEdit />
                    </Button>{" "}
                    <Button
                      variant="danger"
                      size="sm"
                      onClick={() => handleAction("cancelOrder", { ids: [order.shipmentId] })}
                    >
                      <FaTrash />
                    </Button>{" "}
                    <Button
                      variant="warning"
                      size="sm"
                      onClick={() => handleAction("generateLabel", { shipment_id: [order.shipmentId] })}
                    >
                      <FaTags />
                    </Button>{" "}
                    <Button
                      variant="success"
                      size="sm"
                      onClick={() => handleAction("printInvoice", { ids: [order.id] })}
                    >
                      <FaFileInvoice />
                    </Button>
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </Table>

        <PaginationComponent
          totalItems={orders.length}
          itemsPerPage={itemsPerPage}
          currentPage={currentPage}
          onPageChange={setCurrentPage}
        />

        {/* Modal for Update Order */}
        <Modal
          show={showModal}
          onHide={() => setShowModal(false)}
          dialogClassName="modal-dialog-centered"
          className={styles.customModal}
        >
          <Modal.Header closeButton>
            <Modal.Title>Update Order</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <Form>
              <Form.Group>
                <Form.Label>Recipient Name</Form.Label>
                <Form.Control
                  type="text"
                  name="recipientName"
                  value={formData.recipientName || ""}
                  onChange={handleFormChange}
                />
              </Form.Group>
              <Form.Group>
                <Form.Label>Address</Form.Label>
                <Form.Control
                  type="text"
                  name="address"
                  value={formData.address || ""}
                  onChange={handleFormChange}
                />
              </Form.Group>
              <Button
                onClick={() =>
                  handleAction("updateOrder", { ...formData, shipmentId: formData.shipmentId })
                }
                disabled={processing}
              >
                {processing ? "Processing..." : "Save Changes"}
              </Button>
            </Form>
          </Modal.Body>
        </Modal>
      </div>
    </Layout>
  );
};

export default ShippingPage;
